import {
    init,
    h,
} from "snabbdom";

const patch = init([]);

// VNode
const vnode = h('div#container', [
    h('h1', 'Hello Snabbdom'),
    h('p', 'p')
])

const app = document.querySelector('#app')
const oldVnode = patch(app, vnode)

setTimeout(() => {
    const vnode = h('div#container', [
      h('h1', 'Hello World'),
      h('p', 'Hello P')
    ])
    patch(oldVnode, vnode)
  
    // div
    patch(oldVnode, null)
  }, 2000);
